import React from 'react';
import styles from 'src/styles/developers.module.css';
import { Helmet } from 'react-helmet';
import Header from 'src/components/Header.jsx';

const config = {
    AvatarURL: 'https://cdn.discordapp.com/avatars/717434146871443456/0a83c747852f4097f0ddba5cc9365760.png'
};

class Popup extends React.Component {
    render() {
        return (
            <div className={styles.popup}>
                <div className={styles.popupContent}>
                    {this.props.children}
                </div>
            </div>
        );
    };
};

class Developer extends React.Component {
    genTitle(i) {
        switch (i) {
            case 'debug':
                return '–†–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫ —Å–∞–π—Ç–∞';
                break;
            
            case 'masero':
                return '–†–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫ –±–æ—Ç–∞'
                break;
        };
    };

    render() {
        return (
            <div className={`${styles.developer} ${styles[this.props.i]}`} title={this.genTitle(this.props.i)}>
                <div className={styles.iconWrapper}>
                    <img src={this.props.AvatarURL} className={styles.icon} />
                </div>
                <h1 className={styles.name}>
                    {this.props.name}
                </h1>
            </div>
        );
    };
};

class DevelopersList extends React.Component {
    render() {
        return (
            <div className={styles.developers}>
                <div className={styles.developersContent}>
                    {this.props.children}
                </div>
            </div>
        );
    };
};

class Developers extends React.Component {
    render() {
        return (
            <div className={styles.root}>
                <Helmet>
                    <title>
                        Kanami Community - –†–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫–∏
                    </title>
                    <link rel="shortcut icon" href={config.AvatarURL} type="image/x-icon"/>
                    <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
                </Helmet>
                <Header />
                <DevelopersList>
                    <Developer name="Debug_pro" i="debug" AvatarURL="https://cdn.discordapp.com/avatars/487175499794350080/ce755f9cd90dff2d8b4dc957dae22c04.png" />
                    <Developer name="–ê–ª—ã–π –ø—Ä–∏–Ω—Ü –ú–∞—Å–µ—Ä–æ –ö—É–Ωüíé" i="masero" AvatarURL="https://cdn.discordapp.com/avatars/595939165917544460/d5eab20f43f0f5457d822ddd0edd1982.png" />
                </DevelopersList>
            </div>
        );
    };
};

export default Developers;